@import url("https://fonts.googleapis.com/css2?family=Roboto&family=Lobster&display=swap");
.ProfilePhoto > img {
  width: 100%;
  aspect-ratio: 1;
  border-radius: 8px 8px 0 0;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}
html {
  background-color: #10101a;
}
.Info h1 {
  text-align: center;
  font-size: 2rem;
  margin: 1.5rem 0 0.3rem 0;
  line-height: 30px;
  line-height: 100%;
  font-weight: 700;
  color: whitesmoke;
  display: flex;
  justify-content: center;
}

.Info h2 {
  text-align: center;
  color: #f3bf99;
  font-size: 1em;
  font-weight: 400;
  line-height: 15px;
  line-height: 100%;
  display: flex;
  justify-content: center;
}
.Info p {
  text-align: center;
  font-size: 1em;
  margin: 0.5rem 0;
  color: whitesmoke;
  display: flex;
  justify-content: center;
}
.--info-portfolio {
  color: #ffffff;
  text-decoration: none;
}

.--info-portfolio:active {
  color: #ffffff;
}
.--info-buttons {
  display: flex;
  justify-content: space-around;
  padding: 1.2rem;
}

.Info button {
  width: 115px;
  height: 34px;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  line-height: 16px;
}

.--info-email {
  background-color: #ffffff;
  color: #374151;
}

.--info-linkedin {
  background-color: #5093e2;
  color: #ffffff;
}

.About {
  padding: 0.2rem 2rem 1rem;
}

.About h2 {
  font-size: 1.2rem;
  color: whitesmoke;
  display: flex;
  justify-content: flex-start;
}

.About p {
  font-size: 0.8em;
  line-height: 15px;
  color: whitesmoke;
  display: flex;
  justify-content: flex-start;
}

.Interests {
  padding: 0.2rem 2rem 2rem;
}

.Interests h2 {
  font-size: 1.2rem;
  color: whitesmoke;
  display: flex;
  justify-content: flex-start;
}

.Interests p {
  font-size: 0.8em;
  line-height: 15px;
  color: whitesmoke;
  display: flex;
  justify-content: flex-start;
}

.Footer {
  background-color: #161619;
  border-radius: 0 0 8px 8px;
}

.Footer ul {
  list-style: none;
  display: flex;
  justify-content: space-evenly;
  padding-top: 1em;
  padding-bottom: 1em;
}

.Footer a {
  color: #918e9b;
}
@media screen and (max-width: 481px) {
  .container {
    margin: 1rem auto;
    max-width: 80%;
    background-color: #1a1b21;
    border-radius: 8px;
    min-width: 345px;
  }
}
@media screen and (min-width: 481px) {
  .container {
    margin: 1rem auto;
    max-width: 40%;
    background-color: #1a1b21;
    border-radius: 8px;
    min-width: 345px;
  }
}
@media screen and (min-width: 769px) {
  .container {
    margin: 1rem auto;
    max-width: 33%;
    background-color: #1a1b21;
    border-radius: 8px;
    min-width: 345px;
  }
}
.rotateimg {
  -webkit-transform: rotate(-0.5deg);
  -moz-transform: rotate(-0.5deg);
  -ms-transform: rotate(-0.5deg);
  -o-transform: rotate(-0.5deg);
  transform: rotate(-0.5deg);
}
